import React from 'react';
import {useHistory} from "react-router";

export const InvestPlanItem = ({title, percent,day}) => {
    const{push}=useHistory()
    const handlePush=(e) => {
        e.preventDefault()
        push('/register')
    }
    return (
        <a href={'/'} onClick={handlePush} className="invest_plan_item wow slideInLeft" data-wow-duration="3s">
            <div className="invest_plan_item_ins">
                <span className="inv_name">{title}</span>
                <span className="orange_b">
  								<span className="orange_day">{percent} %  </span>
  								<span className="orange_nubmer">{day}</span>
  								<span className="orange_white"></span>
  							</span>
                <ul>
                    <li>: 500 .</li>
                    <li>: 50 000 .</li>
                    <li>   1%</li>
                    <li className="red_t">.  27/7</li>
                    <li>  </li>
                    <li>   </li>
                    <li>  Online</li>
                    <li> </li>
                    <li className="green_t"> </li>
                </ul>
                <span className="register_btn"></span>
            </div>
        </a>
    );
};

